.accordion {
  margin: 40px auto 40px auto;
  max-width: 1240px;
  padding: 0;

  .extra-text {
    display: flex;
    gap: 5px;
    justify-content: flex-start;
  }

  border-radius: 4px;

  & & {
    width: 90%;
    margin: 0 auto;
  }

  &__element:last-child {
    margin-bottom: 0;
  }

  &__element {
    margin-bottom: 0;
    // border-bottom: 1px solid $color-light-gray;

  }

  &__element.is-active>&__button {
    opacity: 0.5;
  }

  &__element svg {
    transition-duration: 1s;
    transform: rotate(-40deg);
    align-self: flex-start;
    margin-top: 25px;
  }

  &__element.is-active svg {
    transform: rotate(-220deg);
    transition-duration: 1s;
  }

  &__button {
    width: 100%;
    // padding: 10px;
    padding: 0;
    border: none;
    background-color: transparent;

    text-transform: uppercase;

    cursor: pointer;

    // font-family: $ff-syncopate;
    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    // color: $color-cello;
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;

    svg {
      margin-left: 10px;
    }

    p {
      margin: 5px 0;
    }
  }

  &__wrapper {
    padding: 10px;
  }

  &__text-content {
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    margin: 5px 0;
  }

  .accordion__button {

    color: $color-gray;

    svg {
      // stroke: $color-gray;
      fill: transparent;
      transition-duration: 0.5s;
    }

    &:hover,
    &:focus,
    &:active {
      color: $red;

      svg {
        stroke: $red;
        ;
        fill: transparent;
        transition-duration: 0.5s;
        opacity: 50%;
      }
    }

    .svg {
      width: 100%;
      height: 20px;
    }

  }
}

.accordion__text-wrapper {
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  text-transform: none;
  color: $color-black;
  max-width: 90%;

  p {
    margin: 23px 0;
  }
}

.accordion__button {

  &:hover,
  &:focus,
  &:active {
    p {
      color: $red;
    }
  }
}


.accordion__text-content {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: $color-gray;
  max-width: 90%;
}

.accordion__wrapper {
  padding: 0;
  padding-bottom: 20px;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-left: 0;
  margin-right: 0;


  .accordion__list {
    border-radius: 4px;
    background-color: $color-light-gray-2;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 15px 10px;
    overflow: hidden;

    li {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      grid-auto-flow: column;
      text-align: center;
      position: relative;

      p {
        position: relative;


        // &:not(:first-child)::after {
        //   content: "";
        //   position: absolute;
        //   width: 1px;
        //   height: 400%;
        //   background-color: $color-light-gray;
        //   bottom: -20px;
        //   z-index: 10;
        //   left: 0;
        // }
      }

      &:not(:last-child)::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: $color-light-gray;
        bottom: -10px;
        z-index: 10;
      }

      &:first-of-type {
        p {
          color: $color-black;
          font-size: 12px;
          opacity: 90%;
          text-transform: uppercase;
          text-align: center;
          display: flex;
          justify-content: center;
          align-items: center;
          max-height: 70px;

          transform: rotate(180deg);
          font-size: 10px;
          writing-mode: tb-rl;


          @include vp-1024 {
            transform: rotate(0deg);
            font-size: 12px;
            writing-mode: horizontal-tb;
          }
        }
      }
    }

    div {
      display: flex;
      justify-content: space-between;
      padding: 4px 8px;
    }

    p {
      font-size: 12px;
      font-weight: 400;
      line-height: 12px;
      color: $color-gray;
      margin: 0;
    }
  }
}


.new {
  .accordion__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

    li {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 18px;

      p {
        position: relative;

        &::after {
          content: "";
          position: absolute;
          width: 100%;
          height: 1px;
          background-color: $color-light-gray;
          bottom: -10px;
          z-index: 10;
          left: 0;
        }
      }
    }
  }
}
